.title {
  color: #000000;
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
  padding: 0 20px;
}
.block {
  background-color: #c4c4c4;
  height: 516px;
}
@media (max-width: 959px) {
  .block {
    width: 100%;
    height: auto;
    margin-bottom: 25px;
  }
}
body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.38;
  font-family: "Roboto", "Helvetica", sans-serif;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
input,
textarea,
select,
button {
  font: inherit;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.header {
  padding-top: 20px;
}
@media (max-width: 959px) {
  .header {
    display: flex;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
  }
}
.header__logo {
  margin-left: 40px;
  margin-bottom: 2px;
}
@media (max-width: 959px) {
  .header__logo {
    margin: 0;
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    margin: 0;
  }
}
.header__main-nav {
  margin-bottom: 23px;
}
@media (max-width: 767px) {
  .header__main-nav {
    margin-bottom: 12px;
  }
}
.main {
  display: grid;
  grid-template-columns: 375px repeat(3, 1fr);
  grid-template-rows: auto auto;
  grid-gap: 25px 15px;
  padding-bottom: 60px;
  position: relative;
}
@media (max-width: 959px) {
  .main {
    grid-template-columns: 546px 1fr;
    grid-template-rows: auto;
    padding: 0 20px;
    grid-gap: 0;
  }
}
@media (max-width: 767px) {
  .main {
    padding: 0 15px;
    grid-template-columns: 1fr;
  }
}
.main__promo {
  grid-column: 1 / 4;
}
@media (max-width: 959px) {
  .main__promo {
    grid-column: 1 / 2;
  }
}
@media (max-width: 767px) {
  .main__promo {
    grid-column: 1;
    margin-bottom: 20px;
  }
}
.main__quores {
  grid-column: 4 / 5;
}
@media (max-width: 959px) {
  .main__quores {
    grid-column: 2 / 3;
  }
}
@media (max-width: 767px) {
  .main__quores {
    grid-column: 1;
  }
}
.main__news {
  grid-column: 1 / 2;
}
@media (max-width: 959px) {
  .main__news {
    grid-column: 1 / 3;
  }
}
@media (max-width: 767px) {
  .main__news {
    grid-column: 1;
  }
}
.main__info {
  grid-column: 2 / 3;
}
@media (max-width: 959px) {
  .main__info {
    grid-column: 1 / 3;
  }
}
@media (max-width: 767px) {
  .main__info {
    grid-column: 1;
  }
}
.main__sales {
  grid-column: 3 / 4;
}
@media (max-width: 959px) {
  .main__sales {
    grid-column: 1 / 3;
  }
}
@media (max-width: 767px) {
  .main__sales {
    grid-column: 1;
  }
}
.main__block-3 {
  grid-column: 4 / 5;
}
@media (max-width: 959px) {
  .main__block-3 {
    grid-column: 1 / 3;
  }
}
@media (max-width: 767px) {
  .main__block-3 {
    grid-column: 1;
  }
}
.main__box {
  grid-column: 1 / 3;
}
@media (max-width: 767px) {
  .main__box {
    grid-column: 1;
  }
}
.footer {
  padding-bottom: 40px;
}
.footer__wrapper {
  background-color: #0078dc;
  height: 330px;
}
.container {
  max-width: 960px;
  margin: 0 auto;
}
@media (max-width: 959px) {
