.main-nav {
  position: relative;
}
.main-nav__list {
  background-color: #0078dc;
  height: 39px;
}
@media (max-width: 959px) {
  .main-nav__list {
    display: none;
  }
}
.main-nav__toggle {
  display: none;
  width: 42px;
  height: 42px;
  background-color: #0078dc;
  border: none;
  cursor: pointer;
}
@media (max-width: 959px) {
  .main-nav__toggle {
    display: block;
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .main-nav__toggle {
    margin-top: 0;
  }
}
@media (max-width: 959px) {
  .main-nav--opened .main-nav__list {
    display: block;
    position: absolute;
    top: 70px;
    left: -288px;
    width: 330px;
    height: 90px;
  }
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    top: 52px;
  }
}
