.box {
  display: none;
}
@media (max-width: 959px) {
  .box {
    height: 98px;
    padding-top: 25px;
    background-color: #f0f0f0;
    display: flex;
    justify-content: space-between;
  }
  .box::after {
    display: block;
    content: "";
    width: 44px;
    height: 44px;
    background-color: #c4c4c4;
  }
}
.box__wrapper {
  width: 91.2%;
  height: 44px;
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
}
.box__wrapper::before,
.box__wrapper::after {
  display: block;
  content: "";
  width: 44px;
  height: 44px;
  background-color: #c4c4c4;
}
@media (max-width: 767px) {
  .box__wrapper {
    width: 73.88%;
  }
}
@media (max-width: 959px) {
  .accordeon__item {
    background: transparent;
  }
  .accordeon__item-title {
    background: #c4c4c4;
  }
  .accordeon-content {
    background: #c4c4c4;
    display: none;
  }
  .accordeon-content--active .accordeon-content {
    display: block;
    animation: open 0.5s ease-out;
    background: #efefef;
  }
  @keyframes open {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
}
